<template>
	<section
		v-show="info_shown"
		class="info_for_session"
		data-cy="info"
	>
		<h2>{{ heading }}</h2>
		<p>{{ print_endpoint }}</p>
		<p>{{ print_session_token }}</p>
		<p>{{ print_agent_code }}</p>
		<p>{{ print_filtered_products }}</p>
		<p>{{ print_version }}</p>
	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';
	import Constants from '@/logic/Constants.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			heading() {
				if (STATE_core().current_step() === 111) {
					return 'cookie session manager | ' + STATE_core().get_endpoint_type();
				}
				var id = STATE_core().product_group_id();
				return Constants.group_names()[id];
			},
			print_session_token()
			{
				return STATE_core().get_session_token() || '-';
			},
			print_agent_code()
			{
				return STATE_params().get_agent_code() || '-';
			},
			print_filtered_products()
			{
				return STATE_params().get_filtered_products() || '-';
			},
			print_version()
			{
				return STATE_core().give_app_version();
			},
			print_endpoint()
			{
				return STATE_core().get_endpoint_type();
			},
			info_shown()
			{
				return true; // temporary change, always show Info bar unless SPA pilot finishes, then add some debug feature to turn it on
				// return STATE_core().get_endpoint_type() !== 'production' && STATE_core().get_endpoint_type() !== 'pg_production';
			},
		},
		watch: {
		},
	}
</script>
