<template>
	<div
		v-if="not_shown_in_technical_steps"
		class="upper_nav text-end"
		data-cy="upper_navigation"
	>

		<!-- left -->
		<div class="wrap">
			<span>Naspäť</span>
			<button
				v-if="show_back"
				:disabled="loading"
				@click="prev"
				title="Naspäť"
				type="button"
				class="btn btn-mini btn-secondary btn-left"
			>
			</button>
		</div>

		<!-- delete -->
		<div class="wrap">
			<span>Vymazať údaje</span>
			<button
				@click="clear"
				:disabled="loading"
				title="Vymazať údaje"
				type="button"
				class="btn btn-mini btn-secondary btn-delete"
			>
			</button>
		</div>

		<!-- right / down -->
		<div class="wrap">
			<span v-if="show_next" >Pokračovať</span>
			<button
				v-if="show_next"
				:disabled="loading"
				@click="next"
				title="Pokračovať"
				type="button"
				class="btn btn-mini btn-secondary btn-right"
			>
			</button>
			<span v-if="show_down" >{{ step === 2 ? 'Vyberte produkt' : 'Potvrďte' }}</span>
			<button
				v-if="show_down"
				:disabled="loading"
				@click="down"
				:title="step === 2 ? 'Vyberte produkt' : 'Potvrďte'"
				type="button"
				class="btn btn-mini btn-secondary btn-right btn-down"
			>
			</button>
		</div>

	</div>
</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_nav from '@/store/STATE_nav.js';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			prev()
			{
				STATE_nav().set_requested_direction('backward');
			},
			next()
			{
				STATE_nav().set_requested_direction('forward');
			},
			clear() {
				STATE_modals().open_it('Cleaning_session');
			},
			down()
			{
				let scroll_targets = {
					2: 'product_list_scroll_anchor',
					4: 'proposal_scroll_anchor',
				};
				let scroll_element = document.querySelector('#osm_app .' + scroll_targets[this.step]);
				let position = scroll_element.getBoundingClientRect().top + window.pageYOffset - 60;
				window.scrollTo({top: position, behavior: 'smooth'});
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			show_back()
			{
				if (this.step === 1 && STATE_forms().first_tab_is_current()) {
					return false;
				}
				return this.step > 0 && this.step < 5;
			},
			show_next()
			{
				return this.step > 0 && this.step < 4 && this.step !== 2;
			},
			show_down()
			{
				return this.step === 2 || this.step === 4;
			},
			loading()
			{
				return STATE_modals().is_loading();
			},
			not_shown_in_technical_steps()
			{
				return this.step < 10;
			},
		},
		watch: {
		},
	}
</script>
